.div{
  -webkit-animation: loadingAnimation 1000ms linear;
          animation: loadingAnimation 1000ms linear;
}
/* .hidden{
  visibility: hidden;
} */
@-webkit-keyframes loadingAnimation {
  0%{
      width: 0%;
      height: .3rem;
      background-color: red;
  }
  10%{
      width: 10%;
      height: .3rem;
      background-color: blue;
  }
  25%{
      width: 25%;
      height: .3rem;
      background-color: yellow;
  }
  35%{
      width: 35%;
      height: .3rem;
      background-color: rgb(197, 26, 40);
  }
  50%{
      width: 50%;
      height: .3rem;
      background-color: rgb(10, 154, 190);
  }
  70%{
      width: 70%;
      height: .3rem;
      background-color: rgb(187, 30, 82);
  }
  100%{
      width: 100%;
      height: .3rem;
      background-color: green;
  }
}
@keyframes loadingAnimation {
  0%{
      width: 0%;
      height: .3rem;
      background-color: red;
  }
  10%{
      width: 10%;
      height: .3rem;
      background-color: blue;
  }
  25%{
      width: 25%;
      height: .3rem;
      background-color: yellow;
  }
  35%{
      width: 35%;
      height: .3rem;
      background-color: rgb(197, 26, 40);
  }
  50%{
      width: 50%;
      height: .3rem;
      background-color: rgb(10, 154, 190);
  }
  70%{
      width: 70%;
      height: .3rem;
      background-color: rgb(187, 30, 82);
  }
  100%{
      width: 100%;
      height: .3rem;
      background-color: green;
  }
}

